

/*이력서*/
.contents{
    background-color: #FFFFFF;
    width:96%;
    height:75%;
    position:absolute;
    top:12%;
    padding: 2%;
    margin-top:0px;
    display: flex;

    flex-direction:column;
    overflow-y: auto;
}
.resume img{
    position:relative;
  
    width:40%;
    height:96%;
    border-radius: 5%;
    
}
.section{
    width:100%;
    text-align: left;
   border-color:#C1C7CD;
   border-top-style:solid;
   border-bottom-style:solid;
   border-width: 0.5px;
    font-size: 100%;
}

.title{
    font-size: 2vh;
    font-weight: bolder;
    
}
.section p{
    margin: 3%;
    margin-left:0%;
    font-size:1.5vh;

    
    
}
.box{
    left:2%;
    padding-top:1%;
    margin-left:4%;
    
}
.info{

    display:flex;
    flex-direction: row;
    text-align: left;
    left:2%;
    top:2%;
    border-top:none;
}


.edu{
    left:3%;
    top:30%;
}
.act{
    left:3%;
    top:56%;
}
.lan{
    left:3%;
    top:70%;
   
 
}
.etc{
    left:3%;
    top:82%;
    border-bottom:none;
}
/*자기소개서*/

.introduction{
    display: flex;
    flex-direction:row;
    width:95%;
    margin-top:5%;
    padding-left:5%;
    flex-wrap:wrap

}
.checkbox{
    display: inline-block;
    color:white;
    background-color:orange;
    width:20px;
    height:20px;
    text-align:center;
    border-radius:20%;

    font-weight:bolder;

}
.checkbox p{
    margin: 10% auto;
}
.introduction >p{
    margin-left:5%;
    width: 70%;
    height:10px;
    margin-top:2px;
    display: inline-block;
    position: relative;
   
    
}


/*포트폴리오*/
.row{display:flex;
    flex-wrap:wrap;
}

.project{

    border:solid 2px;
    border-top:none;
    border-left:none;
    border-right:none;
    border-color:#C1C7CD;
    box-shadow: #FFFFFF;
    box-sizing: border-box;
    width:50%;
    
    
    overflow-y:scroll;
    padding-top:1%;
    padding-bottom:5%;
    
   
}
.project > img{
    width:96%;
    height:50%;
    margin: 0 2%;
    padding:0;
    border: solid;
    box-sizing: border-box;
    border-color:#C1C7CD;
    border-radius: 15%;
}
.projecttitle{
    width:96%;
    font-weight: bold;
    margin: 0% 2%;
    text-align:center;
}
.project p{
    margin:0;
    margin-left:2%;
}
.links{
    width:96%;
    
    margin: auto 2% 1% 2%;
}
.project .inline{
    width:50%;
    margin-left:2%;
    padding:0;
    
    display: inline;
    font-size: small;
}

.link{
    background-color:green;
    border-radius:10%;
    padding:0% 2%;
    text-decoration: none;
    color:#FFFFFF;
    margin:2%;

}
.link:hover{
    background-color:orange;
    border-radius:10%;
    padding:0% 2%;
    text-decoration: none;
    color:black;
}
.explanation{
    font-size:50%;
    width:96%;
    
    margin-left: 2%;
    padding-top:5%;
}
.hashtag{
    font-size:1vh;
    font-weight:bolder;
    color:#C1C7CD;
}
.thissite{
    margin: 30% 5%;
    
    padding:10%;
    box-sizing: border-box;
    
    background-color:lightblue;
    border-radius:10px;
    color:#333388;
}
.thissite p{
    margin:0;
   
}
.thissite .explain{
    padding-bottom:5%;
}
.cf{

    color:#555555;
}