.gray-box {
  background: gray;
  width: 64px;
  height: 64px;
}
.header{
  background-color: white;
    border-radius:8vh 8vh 0 0;
    width:100%;
    height:12%;
    position:absolute;
    top:0%;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
 
  }
.header p{
  position:absolute;
  left:7%;
  top:33%;
  font-Size:2.5vh;
  font-weight: bolder;
  
}
.icon-container{
  width:100%;
  height:80%;
  display: flex;
  justify-content: center;
  gap:10%;
  position:absolute;
  top:5%;
 
}

.icon-container img{
  width: 10%;
  
}
.clicked{
  opacity:1;
}
.nonclicked{
  opacity:0.5;
}
.pageName {
  position:Absolute;
  top:35%;
  left:7%;
  color:Black;
  
  font-size:140%;
}
